import React from 'react'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Avatar from '@material-ui/core/Avatar'
import Typography from '@material-ui/core/Typography'
import Chip from '@material-ui/core/Chip'
import { makeStyles } from '@material-ui/core/styles'
import purple from '@material-ui/core/colors/purple'
import LinkIcon from '@material-ui/icons/Link'
import Carousel from 'nuka-carousel'
import ProfileImage from '../components/profileImage'
import PROJECTS from './projects.json'

const useStyles = makeStyles(theme => ({
  h1: {
    fontSize: theme.typography.pxToRem(36),
    fontWeight: theme.typography.fontWeightMedium,
    marginTop: 16,
  },
  h2: {
    fontSize: theme.typography.pxToRem(18),
    fontWeight: theme.typography.fontWeightLight,
    marginTop: 12,
  },
  h3: {
    fontSize: theme.typography.pxToRem(24),
  },
  h4: {
    fontSize: theme.typography.pxToRem(18),
    fontWeight: theme.typography.fontWeightLight,
  },
  titleContainer: {
    display: 'flex',
    alignItems: 'flex-end',
    justifyContent: 'space-between',
  },
  imageContainer: {
    position: 'relative',
    height: 400,
  },
  image: {
    position: 'relative',
    width: '100%',
    top: '50%',
    transform: 'translateY(-50%)',
  },
  background: {
    backgroundColor: purple[50],
    clipPath: 'polygon(0 0%, 100% 4%, 100% 96%, 0% 100%)',
  },
  carousel: {
    marginBottom: 24,
  },
  body: {
    marginTop: 6,
    marginBottom: 6,
  },
  linkChip: {
    marginTop: 16,
  },
  linkChipAnchor: {
    color: theme.palette.common.white,
  },
  chip: { margin: 4 },
}))

const IndexPage = () => {
  const classes = useStyles()
  return (
    <React.Fragment>
      <Container>
        <Box
          textAlign="center"
          display="flex"
          flexDirection="column"
          alignItems="center"
          my={8}
        >
          <ProfileImage />
          <Typography className={classes.h1} variant="h1">
            Jungwoo Lee
          </Typography>
          <Typography className={classes.h2} variant="h2">
            Web/Game Developer
          </Typography>
          <Chip
            className={classes.linkChip}
            color="secondary"
            avatar={
              <Avatar>
                <LinkIcon />
              </Avatar>
            }
            label={
              <a
                className={classes.linkChipAnchor}
                href="https://www.linkedin.com/in/lustdante/"
                target="_blank"
                rel="noopener noreferrer"
              >
                Linkedin
              </a>
            }
          />
        </Box>
      </Container>
      {PROJECTS.map((projectData, index) => {
        let className = ''
        if (index % 2 === 0) {
          className = classes.background
        }
        return (
          <Box pt={10} pb={6} key={projectData.name} className={className}>
            <Container>
              <Box maxWidth={600} m="0 auto">
                <Carousel className={classes.carousel} slidesToShow={1}>
                  {projectData.images.map(img => (
                    <div key={img.alt} className={classes.imageContainer}>
                      <img
                        className={classes.image}
                        src={img.src}
                        alt={img.alt}
                      />
                    </div>
                  ))}
                </Carousel>
                <div className={classes.titleContainer}>
                  <Typography
                    className={classes.h3}
                    variant="h3"
                    component="a"
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://blogs.msdn.microsoft.com/andrewparsons/2011/06/03/imagine-cup-2011-game-design-competitor-showcase-part-9/"
                  >
                    {projectData.name}
                  </Typography>
                  <Typography className={classes.h4} variant="h4">
                    {projectData.date}
                  </Typography>
                </div>
                <Typography className={classes.body} variant="body1">
                  {projectData.description}
                </Typography>
                {projectData.skills.map(skill => (
                  <Chip
                    key={`${projectData.name}-${skill}`}
                    className={classes.chip}
                    label={skill}
                    color="primary"
                  />
                ))}
              </Box>
            </Container>
          </Box>
        )
      })}
    </React.Fragment>
  )
}

export default IndexPage
